<template>
  <el-form
    class="form"
    :inline="true"
    v-loading="loading"
    @submit.native.prevent="onSubmit"
    :model="form"
    label-width="150px"
  >
    <el-row :gutter="54">
      <el-col>
        <el-form-item label="用户注册">
          <el-input
            v-model="form.register"
            type="text"
            placeholder="请输入内容"
          ></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="找回密码">
          <el-input
            v-model="form.forgetpwd"
            type="text"
            placeholder="请输入内容"
          ></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="收益提醒">
          <el-input
            v-model="form.item_apr"
            type="text"
            placeholder="请输入内容"
          ></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="提现成功">
          <el-input
            v-model="form.cost_ok"
            type="text"
            placeholder="请输入内容"
          ></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="提现失败">
          <el-input
            v-model="form.cost_no"
            type="text"
            placeholder="请输入内容"
          ></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="充值成功">
          <el-input
            v-model="form.invest_ok"
            type="text"
            placeholder="请输入内容"
          ></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="充值失败">
          <el-input
            v-model="form.invest_no"
            type="text"
            placeholder="请输入内容"
          ></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="投资项目">
          <el-input
            v-model="form.item_apply"
            type="text"
            placeholder="请输入内容"
          ></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="短信账号-注册">
          <el-input
            v-model="form.username1"
            type="text"
            placeholder="请输入内容"
          ></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="短信密钥-注册">
          <el-input
            v-model="form.password1"
            type="text"
            placeholder="请输入内容"
          ></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="短信账号-理财">
          <el-input
            v-model="form.username2"
            type="text"
            placeholder="请输入内容"
          ></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="短信密钥-理财">
          <el-input
            v-model="form.password2"
            type="text"
            placeholder="请输入内容"
          ></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item>
          <el-button type="primary" native-type="submit">保存</el-button>
        </el-form-item>
      </el-col>
    </el-row>
  </el-form>
</template>
<script>
import Fetch from '../../utils/fetch'
import { Message } from 'element-ui'

export default {
  data() {
    return {
      form: {},
      loading: false,
    }
  },
  mounted() {
    this.loading = true
    Fetch('/sys/config/info', {
      type: 'sms_cl',
    })
      .then(r => {
        this.form = r.data
      })
      .catch(res => {
        Message.error(res.msg)
      })

    this.loading = false
  },
  methods: {
    onSubmit() {
      Fetch('/sys/config/update', {
        type: 'sms_cl',
        ...this.form,
      })
        .then(() => {
          Message.success('操作成功')
        })
        .catch(() => {
          Message.error('操作失败')
        })
      this.loading = false
    },
  },
}
</script>
<style lang="less" scoped>
.form {
  /deep/ .el-input__inner {
    width: 500px;
  }

  /deep/ .el-textarea {
    width: 300px;
  }
}
</style>
